import { motion } from 'framer-motion';
import useReducedMotion, { getTransition } from '../hooks/useReducedMotion';

/**
 * DialRing - Outer ring with subcategory ticks and labels.
 * Rotates during drag, snaps active tick to top on gesture end.
 * 
 * @param {number|null} hoverSubIndex - Transient hover index during rotation
 * @param {number} activeSubIndex - Currently active subcategory index
 * @param {Array} subcategories - Array of subcategory objects
 */
export default function DialRing({ hoverSubIndex, activeSubIndex, subcategories = [] }) {
  const prefersReducedMotion = useReducedMotion();
  if (!subcategories.length) return null;

  const count = subcategories.length;
  
  // Calculate rotation to keep active tick at top
  // Each subcategory occupies 360/count degrees
  const degreesPerTick = 360 / count;
  
  // Base rotation to put active at top (0 is right, -90 is top)
  let rotation = -90 - (activeSubIndex * degreesPerTick);
  
  // Add hover offset for visual feedback during drag
  if (hoverSubIndex !== null) {
    rotation -= hoverSubIndex * degreesPerTick;
  }

  return (
    <motion.div
      className="absolute inset-0 rounded-full pointer-events-none"
      animate={{ rotate: rotation }}
      transition={getTransition(prefersReducedMotion, {
        // During drag: follow finger with slight elasticity (stiffness 180, damping 20)
        // On snap: quick spring (stiffness 260, damping 18)
        type: 'spring',
        stiffness: hoverSubIndex !== null ? 180 : 260,
        damping: hoverSubIndex !== null ? 20 : 18
      })}
      style={{ zIndex: 5 }}
    >
      {subcategories.map((sub, index) => {
        const angle = (index / count) * 360;
        const isActive = index === activeSubIndex;
        const isHovered = hoverSubIndex !== null && 
                         index === (activeSubIndex + hoverSubIndex) % count;

        return (
          <TickWithLabel
            key={sub.id}
            angle={angle}
            label={sub.label}
            isActive={isActive}
            isHovered={isHovered}
            prefersReducedMotion={prefersReducedMotion}
          />
        );
      })}
    </motion.div>
  );
}

/**
 * Individual tick and label for a subcategory.
 */
function TickWithLabel({ angle, label, isActive, isHovered, prefersReducedMotion }) {
  // Calculate position on circle (radius ~45% of container)
  const radius = 45;
  const angleRad = (angle - 90) * (Math.PI / 180); // -90 to start at top
  const x = 50 + radius * Math.cos(angleRad);
  const y = 50 + radius * Math.sin(angleRad);

  // Tick properties
  const tickLength = isActive ? 8 : 6;
  const tickOpacity = isActive ? 1 : isHovered ? 0.7 : 0.4;
  const labelOpacity = isActive ? 1 : isHovered ? 0.6 : 0.3;

  return (
    <div
      className="absolute"
      style={{
        left: `${x}%`,
        top: `${y}%`,
        transform: `translate(-50%, -50%) rotate(${angle}deg)`
      }}
    >
      {/* Tick mark - animated length and opacity */}
      <motion.div
        className="bg-white"
        animate={{
          height: tickLength,
          opacity: tickOpacity
        }}
        transition={getTransition(prefersReducedMotion, {
          type: 'spring',
          stiffness: 300,
          damping: 25
        })}
        style={{
          width: '1px',
          transformOrigin: 'center'
        }}
      />
      
      {/* Label - animated opacity */}
      <motion.div
        className="absolute whitespace-nowrap select-none"
        animate={{
          opacity: labelOpacity,
          fontWeight: isActive ? 600 : 400
        }}
        transition={getTransition(prefersReducedMotion, {
          duration: 0.12,
          ease: 'easeOut'
        })}
        style={{
          left: '50%',
          top: '100%',
          transform: `translate(-50%, 4px) rotate(${-angle}deg)`,
          fontSize: '10px',
          color: 'white',
          fontFamily: '-apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, sans-serif',
          textTransform: 'uppercase',
          letterSpacing: '0.05em',
          maxWidth: '80px',
          overflow: 'hidden',
          textOverflow: 'ellipsis'
        }}
      >
        {label}
      </motion.div>
    </div>
  );
}

