import { motion, AnimatePresence } from 'framer-motion';
import useReducedMotion from '../hooks/useReducedMotion';

/**
 * EventReadout - Displays current event details with gesture support.
 * Animates on event change with fade/slide transitions.
 * 
 * @param {Object} activeEvent - Current event object
 * @param {Object} activePrimary - Current primary category
 * @param {Object} bindProps - Gesture props from useDialGestures (bindLowerAreaProps)
 */
export default function EventReadout({ activeEvent, activePrimary, bindProps = {} }) {
  const prefersReducedMotion = useReducedMotion();
  
  if (!activeEvent) {
    return (
      <div 
        className="w-full px-8 py-6 text-center"
        {...bindProps}
        role="region"
        aria-label="Event details"
        aria-live="polite"
      >
        <p className="text-white" style={{ opacity: 0.5 }}>
          No events found
        </p>
      </div>
    );
  }

  return (
    <div 
      className="w-full px-8 py-6 text-center"
      {...bindProps}
      style={{
        touchAction: 'none',
        userSelect: 'none'
      }}
      role="region"
      aria-label="Event details"
      aria-live="polite"
    >
      <AnimatePresence mode="wait">
        <motion.div
          key={activeEvent.id}
          initial={prefersReducedMotion ? false : { opacity: 0, y: 20 }}
          animate={{ opacity: 1, y: 0 }}
          exit={prefersReducedMotion ? false : { opacity: 0, y: -20 }}
          transition={prefersReducedMotion ? {
            duration: 0
          } : {
            duration: 0.15,
            ease: [0.4, 0, 0.2, 1] // easeOut
          }}
        >
          {/* Event name - bold, larger (18-20px minimum for accessibility) */}
          <h2 
            className="text-white font-bold mb-2"
            style={{
              fontSize: '20px',
              fontFamily: '-apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, sans-serif',
              lineHeight: 1.3
            }}
          >
            {activeEvent.name}
          </h2>

          {/* Tags/Type (14px minimum for accessibility) */}
          {activeEvent.tags && activeEvent.tags.length > 0 && (
            <p 
              className="text-white mb-1"
              style={{
                opacity: 0.9,
                fontSize: '14px',
                fontFamily: '-apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, sans-serif'
              }}
            >
              {activeEvent.tags.join('  ')}
              {activePrimary && `  ${activePrimary.label}`}
            </p>
          )}

          {/* Address (14px minimum for accessibility) */}
          {activeEvent.address && (
            <p 
              className="text-white mb-1"
              style={{
                opacity: 0.8,
                fontSize: '14px',
                fontFamily: '-apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, sans-serif'
              }}
            >
              {activeEvent.address}
            </p>
          )}

          {/* Time & Distance (14px minimum for accessibility) */}
          <div 
            className="text-white flex items-center justify-center gap-3"
            style={{
              opacity: 0.7,
              fontSize: '14px',
              fontFamily: '-apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, sans-serif'
            }}
          >
            {activeEvent.time && <span>{activeEvent.time}</span>}
            {activeEvent.time && activeEvent.distance && <span></span>}
            {activeEvent.distance && <span>{activeEvent.distance}</span>}
          </div>
        </motion.div>
      </AnimatePresence>
    </div>
  );
}
