import { motion } from 'framer-motion';
import useReducedMotion, { getTransition } from '../hooks/useReducedMotion';

/**
 * CategoryLabels - Four primary category labels at N/E/S/W positions.
 * Minimal design: white text, active/inactive opacity only.
 */

export default function CategoryLabels({ categories, activeDirection }) {
  const prefersReducedMotion = useReducedMotion();
  // Position mapping for N/E/S/W
  const positions = {
    north: { top: '0%', left: '50%', transform: 'translate(-50%, -100%)' },
    east: { top: '50%', right: '0%', transform: 'translate(100%, -50%)' },
    south: { bottom: '0%', left: '50%', transform: 'translate(-50%, 100%)' },
    west: { top: '50%', left: '0%', transform: 'translate(-100%, -50%)' }
  };

  return (
    <>
      {categories.map((category) => {
        const isActive = category.direction === activeDirection;
        const position = positions[category.direction];

        return (
          <motion.div
            key={category.direction}
            className="absolute pointer-events-none"
            style={{
              ...position,
              zIndex: 10
            }}
            animate={{
              opacity: isActive ? 1 : 0.6,
              scale: isActive ? 1 : 0.95
            }}
            transition={getTransition(prefersReducedMotion, {
              type: 'spring',
              stiffness: 260,
              damping: 18
            })}
          >
            <span
              className="text-white text-sm tracking-wide select-none"
              style={{
                fontWeight: isActive ? 600 : 400,
                fontFamily: '-apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, sans-serif',
                textTransform: 'uppercase',
                letterSpacing: '0.05em'
              }}
            >
              {category.label}
            </span>
          </motion.div>
        );
      })}
    </>
  );
}

